<template>
  <div class="mdl-layout mdl-js-layout has-drawer mdl-layout--fixed-header mdl-layout--fixed-drawer page">
       <header class="mdl-layout__header">
        <slot name="header">
          <header-navigation></header-navigation>
        </slot>
       </header>
      <div class="mdl-layout__drawer sidebar-navigation sidebar">
        <slot name="sidebar">
          <sidebar-menu></sidebar-menu>
        </slot>
      </div>
      <main class="mdl-layout__content mdl-color--grey-100">
        <vue-toastr ref="toastr"></vue-toastr>
        <slot name="content"></slot>
        <slot></slot>
      </main>
  </div>
</template>
<script>
import { HeaderNavigation, SidebarMenu } from '.'

export default {
  name: 'dashboard-layout',
  components: {
    HeaderNavigation,
    SidebarMenu
  }
}
</script>



